<script>
    import {mapActions, mapGetters} from 'vuex'
    // import router from "../../router/router";

    export default {
        name: "v-header",
        data() {
            return {
                searchResult: '',
                loginStatus: false,
                loginName: '',
                openHeader: false
            }
        },
        methods: {
            ...mapActions({
                SET_SEARCH: 'infoModule/SET_SEARCH',
                OPEN_POPUP: 'popupModule/OPEN_POPUP',
                LOG_OUT: 'userModule/LOG_OUT'
            }),
            search(){
                this.SET_SEARCH(this.searchResult)
            },
            openPopup(){
                this.OPEN_POPUP()
            },
            alert(){
                this.loginStatus = false;
                this.$router.push({name:'posts'});
                this.LOG_OUT();
                localStorage.clear();
            },
            closeHeader(){
                this.openHeader = false;
            }
        },
        computed: {
            ...mapGetters({
                FAVORITE: 'favoriteModule/FAVORITE',
                USER: 'userModule/USER',
                CATEGORIES: 'infoModule/CATEGORIES',
            }),
            user(){
                return this.USER
            },
            categories(){
                const categories = this.CATEGORIES;
                categories.pop()
                return categories;
            }
        },
        watch: {
            '$route': 'closeHeader',
            user() {
                if (localStorage.getItem('API_TOKEN')) {
                    this.loginStatus = true;
                    this.loginName = localStorage.getItem('API_NAME');
                }
            }
        },
        mounted() {
            if (localStorage.getItem('API_TOKEN')) {
                this.loginStatus = true;
                this.loginName = localStorage.getItem('API_NAME');
            }
        },
    }

    // router.afterEach(() => {
    //     // closeHeader();
    //     console.log('Router ' + this)
    // });
</script>
